/* ============================================= */
/*              Mixins                      */
/* ============================================= */

//Used in images and media-queries partials

@mixin display-block-margins {
    display: block;
    margin-left: auto;
    margin-right: auto;
    @content;
}

//Used in images partial

@mixin img-margin-width {
    margin-top: 10px;
    max-width: 100%; 
    @content;
}

//Used in buttons partial

@mixin button-transition {
    transition: background-color .5s;
    transition-timing-function: ease-in;
    @content;
}